// Yahoo
// Let's get started a new searcher as ts plugin

type Token = { // Needs to found a token (no more)
  0: string,
  index: number
}

export class Searcher {
  private result: string;
  private searchString: string;
  private elementsList: Array<NodeList>;

  public constructor(string: string, elements: Array<string>) {
    this.searchString = this.removeSpecialSymbols(string.toLowerCase().trim());
    this.result = ''; // Init this one

    // Get any elements ( fu*# flux )
    this.elementsList = elements.map(
      (e: string): NodeList => {
        return document.querySelectorAll(e);
      }
    );
  }

  private fastSearch = (): void => {
    // Use if you wanna get result faster (you will have spent 2 - 4 ms)
    this.elementsList.map((els: NodeList) => {
      els.forEach((e: any) => {
        if (this.searchString != "") {
          e.innerHTML = this.markRemove(e.innerHTML).replace(
            new RegExp(this.searchString, "gi"),
            this.mark(this.searchString)
          );
        } else {
          e.innerHTML = this.markRemove(e.innerHTML);
        }
      });
    });

    // It looks like 2ms - 4ms is spend
  };

  public search = (type: boolean): void => {
    if (!type) {
      // Public API
      this.elementsList.map((els: NodeList) => {
        // Check all nodes
        els.forEach((e: any) => {
          // Check all node elements
          if (this.searchString !== "") {
            // Mark found  symbols
            e.innerHTML = this.parse(this.searchString, e.innerText);
          } else {
            // Or delete all markers
            e.innerHTML = this.markRemove(e.innerText);
          }
        });
      });

      // This one is spend 4 - 15 ms to search
    } else this.fastSearch();
    // You can set type = true to use fast search
    // ( fast search replaces any typecases sumbols to lowercase but it's faster )
  };

  private parse = (
    token: string,
    string: string,
    result: string = ""
  ): string => {
    if (result === "") {
      // First iteration
      // Let's find an expression in the given string
      let foundToken: RegExpMatchArray | null = string.match(new RegExp(token, "i"));
      let result = string;

      if (foundToken !== null) {
        // if we have found something ;)
        let offString = string.substring(Number(foundToken.index) + token.length);

        // Update result
        result =
          result.substring(0, foundToken.index) +
          this.mark(foundToken[0]) +
          offString;

        // Recursive call itself ( next condition )
        this.parse(token, offString, result);
      } else {
        // If we haven't found something :<
        return result;
      }
    } else {
      // Here we gonna find a token again
      let foundToken: RegExpMatchArray | null = string.match(new RegExp(token, "i"));

      // If we have found some one
      if (foundToken !== null) {
        // Get offseted string and result ( main actions here )
        let offString: string = string.replace(
          foundToken[0],
          this.mark(foundToken[0])
        );

        result = result.replace(string, offString);

        // Redeclare offString to give it itself ( recursive )
        offString = offString.substring(
          Number(foundToken.index) + this.mark(foundToken[0]).length
        );

        // Call itselft ( Yamete kudasai )
        this.parse(token, offString, result);
      } else {
        this.result = result;
        // Yeah, we've found it :)
      }
    }

    // Yep-yep
    return this.result;
  };

  // Utils
  private mark = (token: string): string => {
    // Marking found token
    return `<mark>${token}</mark>`;
  };

  private markRemove = (htmlItem: string): string => {
    return htmlItem // Remove any markers
      .replace(new RegExp("<mark>", "g"), "")
      .replace(new RegExp("</mark>", "g"), "");
  };

  private removeSpecialSymbols = (string: string): string => {
    // Here i gonna remove any stuff like shitsymbols else it get an error ;)
    return string.replace(/[?/\\!@$%&*+^\[\]|()]/g, "");
  };
}
